import React, { Component } from 'react';
import Jumbotron from 'react-bootstrap/Jumbotron';
import logo from '../images/e2.0.png';
import { HashRouter as Router, Route, Link } from "react-router-dom";
import { Navbar, Nav, Container } from 'react-bootstrap'
import logo1 from '../images/e3.5.png';

export default class TodosList extends Component {
  render() {
    return (
      <>
        <div clasName="container1">
          <div className="parallax1_5">
            <Navbar id="navbarId" fixed="top" className="navbar-collapse" collapseOnSelect expand="lg" variant="dark">
              <Navbar.Brand href="/mern-My-Portfolio/#"><img src={logo1} width="200" height="" /></Navbar.Brand>
              <Navbar.Toggle aria-controls="responsive-navbar-nav" />
              <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="mr-auto">
                </Nav>
                <Nav aria-label="Toggle navigation">
                  <Link className="links" to="/portfolio">Portfolio</Link>
                  <Link className="links" eventKey={2} to="/contact"> Contact </Link>
                </Nav>
              </Navbar.Collapse>
            </Navbar>
          </div>
        </div>
        <br></br>
        <Container className="mb-4">
          <img className="mb-4" src={logo1} width="200" height="" />
          <h3 className="mb-4">Contact Page </h3><hr></hr>
          <h5 className="m-4">Email <br></br> <a href="mailto:Ericnrgnash@gmail.com">Ericnrgnash@gmail.com</a> </h5>
          <h5 className="m-4">LinkedIn Profile <br></br><a href="https://www.linkedin.com/in/ericsimmonsnash/">Linkedin</a>
          </h5>
          <h5 className="m-4 ">Phone Number <br></br><a href="tel:615-481-3124"> 615-481-3124</a> </h5>
        </Container>


        <div className="parallax1_5">
        </div>
      </>
    )
  }
}